import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';
import { connect } from 'react-redux';
import { hideDuplicateFilter } from '../../../../../Services/Actions/assetsActions'
import Swal from 'sweetalert2';

class DuplicatePostAlert extends Component {
    onHideDuplicate = event => this.props.hideDuplicateFilter()
    selectAction = () => {
        const {isCollection, collectionId, history, userData} = this.props
        if(userData.user.id == 144127 || (userData.collaborator != '' && userData.collaborator.activeAccount == 144127)) this.customUserError()
        else history(`/rule/add?duplicate=true${isCollection ? `&collectionId=${collectionId}` : ``}`)
    }

    customUserError = () => {
        Swal.fire({
            title: 'Oops!',
            text: "You do not have access to this feature. Please contact your account manager to get access.",
            icon: 'error',
        })
    }

    render() {
        const { count, isCollection, collectionId, assets, history } = this.props
        return count && parseInt(count) > 0 ? <Row>
            <Col>
                <div className="d-flex alert alert-danger py-3">
                    <div className="d-flex flex-column flex-stack flex-grow-1">
                        <p className="mb-0 text-gray-800 text-center fs-7">{count} Duplicate images detected. To clear them out click on {assets.filterBody.hideDuplicate ? <span onClick={()=> this.selectAction()} className='fw-bold text-link text-danger text-decoration-none cursor-pointer'>'Action'</span> : <span className='fw-bold text-link text-danger text-decoration-none cursor-pointer' onClick={this.onHideDuplicate}>'Review'</span>}  </p>
                    </div>
                </div>
            </Col>
        </Row> : null
    }
}

const mapStateToProps = state => {
    return {
        assets: state.assets,
        userData: state.authenticated.userData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        hideDuplicateFilter: () => dispatch(hideDuplicateFilter())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DuplicatePostAlert);